import mission from './mission.vue'
import enquierResult from './enquierResult.vue'
export default {
  data () {
    var numberOnly = (rule, value, callback) => {
      if (isNaN(this.formInline[rule.fullField])) {
        callback(new Error('只能填数字！'))
      } else if (parseFloat(this.formInline[rule.fullField]) < 0) {
        callback(new Error('不能小于0！'))
      } else {
        this.formInline[rule.fullField] = parseFloat(this.formInline[rule.fullField])
        callback()
      }
    }
    var checkDirectAngleEnd = (rule, value, callback) => {
      if (isNaN(this.formInline[rule.fullField])) {
        callback(new Error('只能填数字！'))
      } else if (parseFloat(this.formInline[rule.fullField]) <= parseFloat(this.formInline.takePhotoDirectAngleBegin)) {
        callback(new Error('要比方向角起点大！'))
      } else if (parseFloat(this.formInline[rule.fullField]) < 0) {
        callback(new Error('不能小于0！'))
      } else {
        this.formInline[rule.fullField] = parseFloat(this.formInline[rule.fullField])
        callback()
      }
    }
    return {
      missionList: {
        cellName: '',
        num: '',
        missionName: '',
        missionNum: '',
        cellId: '',
        cellLongit: '',
        downtilt: '',
        celldimen: '',
        angle: '',
        height: '',
        devAngle: '',
        number: '',
        reciver: '',
        finish: ''
      },
      activeName: 'missionList',
      editLabelName: '',
      NewMissionLabelName: '新增任务',
      searchInput: '',
      showPanel: true,

      missionNameList: [],
      taskIDList: [],
      cellIDList: [],
      publisherList: [],
      reciverList: [],
      position: [113.564221, 22.370364],
      center: [], // 地图图标居中
      zoom: 16,
      currentList: {}, // 用于检测是否有修改
      user: '',
      openMap: true,
      dialogTableVisible: false,
      // result: false, // 用于判断表格输入信息是否符合
      checkForm: [],
      userType: '',
      formInline: {
        taskName: '',
        bscID: '',
        cellID: '',
        bscIDCellID: '',
        taskID: '',
        takePhotoLongitude: '',
        takePhotoLatitude: '',
        takePhotoAltitude: '',
        takePhotoElevationAngle: '',
        takePhotoDirectAngleBegin: '',
        takePhotoDirectAngleEnd: '',
        takePhotoDirectAngleRotate: '',
        takePhotoNum: '',
        taskProducerID: '',
        taskProduceTime: '',
        taskConsumerID: '',
        taskPlanCompleteTime: '',
        directAngle: ''
      },
      // formList: ['form1', 'form2', 'form3', 'form4', 'form5', 'form6'], // 表单列表
      rules: {
        taskName: [
          { required: true, message: '不能为空！', trigger: 'blur' }
        ],
        takePhotoLongitude: [
          { required: true, message: '不能为空！', trigger: 'blur' },
          { validator: numberOnly, trigger: 'blur' }
        ],
        takePhotoLatitude: [
          { required: true, message: '不能为空！', trigger: 'blur' },
          { validator: numberOnly, trigger: 'blur' }
        ],
        takePhotoDirectAngleBegin: [
          { required: true, message: '不能为空！', trigger: 'blur' },
          { validator: numberOnly, trigger: 'blur' }
        ],
        takePhotoDirectAngleEnd: [
          { required: true, message: '不能为空！', trigger: 'blur' },
          { validator: checkDirectAngleEnd, trigger: 'blur' }
        ],
        takePhotoDirectAngleRotate: [
          { required: true, message: '不能为空！', trigger: 'blur' },
          { validator: numberOnly, trigger: 'blur' }
        ],
        takePhotoAltitude: [
          { required: true, message: '不能为空！', trigger: 'blur' },
          { validator: numberOnly, trigger: 'blur' }
        ],
        takePhotoElevationAngle: [
          { required: true, message: '不能为空！', trigger: 'blur' },
          { validator: numberOnly, trigger: 'blur' }
        ],
        taskConsumerID: [
          { required: true, message: '不能为空！', trigger: 'blur' }
        ],
        taskPlanCompleteTime: [
          { required: true, message: '不能为空！', trigger: 'blur' }
        ]
      },
      mapEvents: {
        'click': (e) => {
          // 第一个参数是latitude，第二个是longitude
          let change = this.gcjToWgsExactly(e.lnglat.lat, e.lnglat.lng)
          let longitude = change.lon
          let latitude = change.lat
          this.formInline.takePhotoLongitude = Math.ceil(longitude * 100000) / 100000
          this.formInline.takePhotoLatitude = Math.ceil(latitude * 100000) / 100000
          this.position = [e.lnglat.lng, e.lnglat.lat]
        }
      }
    }
  },
  created () {
    this.getReciver()
    this.center = this.position
    this.userType = this.getCookie('userType')
    if (this.userType === '2') {
      this.NewMissionLabelName = ''
    }
  },
  watch: {
    AngleEnd (newVal, oldVal) {
      this.countPhotoNum()
    },
    AngleBegin (newVal, oldVal) {
      this.countPhotoNum()
    },
    AngleRotate (newVal, oldVal) {
      this.countPhotoNum()
    },
    takePhotoLongitude (newVal, oldVal) {
      if (newVal) {
        let change = this.wgsToGcj(this.takePhotoLatitude, newVal)
        let longitude = change.lon
        let latitude = change.lat
        this.position = [longitude, latitude]
      }
    },
    takePhotoLatitude (newVal, oldVal) {
      if (newVal) {
        // 第一个参数是latitude，第二个是longitude
        let change = this.wgsToGcj(newVal, this.takePhotoLongitude)
        let longitude = change.lon
        let latitude = change.lat
        this.position = [longitude, latitude]
      }
    }
  },
  computed: {
    AngleEnd () {
      return this.formInline.takePhotoDirectAngleEnd
    },
    AngleBegin () {
      return this.formInline.takePhotoDirectAngleBegin
    },
    AngleRotate () {
      return this.formInline.takePhotoDirectAngleRotate
    },
    takePhotoLongitude () {
      return this.formInline.takePhotoLongitude
    },
    takePhotoLatitude () {
      return this.formInline.takePhotoLatitude
    }
  },
  methods: {
    toResultTab () {
      this.activeName = 'missionList'
      this.$refs.missionResult.getMission()
    },
    countPhotoNum () {
      if (this.formInline.takePhotoDirectAngleEnd < this.formInline.takePhotoDirectAngleBegin) {
        this.formInline.takePhotoNum = Math.ceil((360 + (this.formInline.takePhotoDirectAngleEnd - this.formInline.takePhotoDirectAngleBegin)) / this.formInline.takePhotoDirectAngleRotate) + 1
      } else if (this.formInline.takePhotoDirectAngleEnd === 360) {
        this.formInline.takePhotoNum = Math.ceil((this.formInline.takePhotoDirectAngleEnd - this.formInline.takePhotoDirectAngleBegin) / this.formInline.takePhotoDirectAngleRotate)
      } else {
        this.formInline.takePhotoNum = Math.ceil((this.formInline.takePhotoDirectAngleEnd - this.formInline.takePhotoDirectAngleBegin) / this.formInline.takePhotoDirectAngleRotate) + 1
      }
    },
    getReciver () {
      this.reciverList.length = 0
      let params = new URLSearchParams()
      let path = this.setAPIPath('/getTakePhotoUser')
      this.$axios
        .get(path, params)
        .then(response => {
          if (response.data.code === 0) {
            for (let i of response.data.data) {
              this.reciverList.push(i)
            }
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    handleClick (tab, event) {
      if (tab.name === 'missionList') {
        this.$refs.missionResult.getMission()
      }
      if (tab.name !== 'editMission') {
        this.showPanel = true
        this.editLabelName = ''
      }
    },
    show (index, row, directAngle) {
      this.showPanel = false
      this.editLabelName = '编辑任务'
      this.activeName = 'editMission'
      for (var i in this.formInline) {
        this.formInline[i] = row[i]
        this.currentList[i] = row[i]
      }
      this.formInline.bscIDCellID = row.bscID + '/' + row.cellID
      this.formInline.directAngle = directAngle
      // 第一个参数是latitude，第二个是longitude
      let change = this.wgsToGcj(this.formInline.takePhotoLatitude, this.formInline.takePhotoLongitude)
      let longitude = change.lon
      let latitude = change.lat
      this.position = [longitude, latitude]
      this.center = this.position
    },
    removeTab () {
      this.showPanel = true
      this.editLabelName = ''
      this.activeName = 'missionList'
    },
    triggerClick () {
      // 将增加任务页面表格默认都是没验证
      this.$refs.form1.validate((valid) => {
        if (valid) {
          var changeFlag = false
          for (var k in this.currentList) {
            if (this.currentList[k] !== this.formInline[k]) {
              changeFlag = true
            }
          }
          if (changeFlag) {
            this.changeMission()
          } else {
            this.openMsg('请修改后再提交！', 'error', this)
          }
        } else {
          return false
        }
      })
      // for (var i = 0; i < this.formList.length; i++) {
      //   this.checkForm[i] = true
      // }
      // for (i = 0; i < this.formList.length; i++) {
      //   this.checkRule(this.formList[i], i)
      // }
      // this.checkForm.forEach(element => {
      //   this.result = this.result || element
      // })
      // if (!this.result) {
      //   var changeFlag = false
      //   for (var k in this.currentList) {
      //     if (this.currentList[k] !== this.formInline[k]) {
      //       changeFlag = true
      //     }
      //   }
      //   if (changeFlag) {
      //     this.changeMission()
      //   } else {
      //     this.openMsg('请修改后再提交！', 'error', this)
      //   }
      // } else {
      //   for (var j = 0; j < this.formList.length; j++) {
      //     this.checkForm[j] = true
      //   }
      // }
      // this.result = false
    },
    checkRule (formName, i) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.checkForm[i] = false
        } else {
          return false
        }
      })
    },
    changeMission () {
      let params = new URLSearchParams()
      let path = this.setAPIPath('/task')
      params.append('taskID', this.formInline.taskID)
      params.append('taskName', this.formInline.taskName)
      params.append('bscID', this.formInline.bscID)
      params.append('cellID', this.formInline.cellID)
      params.append('takePhotoLongitude', parseFloat(this.formInline.takePhotoLongitude))
      params.append('takePhotoLatitude', parseFloat(this.formInline.takePhotoLatitude))
      params.append('takePhotoAltitude', this.formInline.takePhotoAltitude) // 海拔高度
      params.append(
        'takePhotoElevationAngle',
        this.formInline.takePhotoElevationAngle // 下倾角
      )
      params.append(
        'takePhotoDirectAngleBegin',
        this.formInline.takePhotoDirectAngleBegin
      )
      params.append(
        'takePhotoDirectAngleEnd',
        this.formInline.takePhotoDirectAngleEnd
      )
      params.append(
        'takePhotoDirectAngleRotate',
        this.formInline.takePhotoDirectAngleRotate // 偏移角
      )
      params.append('takePhotoNum', this.formInline.takePhotoNum)
      params.append('taskConsumerID', this.formInline.taskConsumerID)
      params.append('taskPlanCompleteTime', parseInt((new Date(this.formInline.taskPlanCompleteTime.replace(/-/g, '/')).getTime()) / 1000))
      this.$axios
        .put(path, params)
        .then(response => {
          if (response.data.code === 0) {
            this.openMsg('修改成功！', 'success', this)
            // this.dialogTableVisible = false
            this.removeTab()
          } else {
            this.errorMsg(response.data.code, this)
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    clear () {
      for (var i in this.currentList) {
        this.formInline[i] = this.currentList[i]
      }
      // 第一个参数是latitude，第二个是longitude
      let change = this.wgsToGcj(this.formInline.takePhotoLatitude, this.formInline.takePhotoLongitude)
      let longitude = change.lon
      let latitude = change.lat
      this.position = [longitude, latitude]
      this.center = this.position
    }
  },
  components: {
    mission,
    enquierResult
  }
}



